.order-item {
    display: flex;
    margin-bottom: 30px;
}

.order-item-cover {
    width: 200px;
    margin-right: 30px;
}

.order-item-title {
    font-size: 24px;
    font-weight: bold;
}

.order-item-price {
    font-size: 24px;
    font-weight: bold;
    margin-left: auto;
}

.order-item-remove {
    cursor: pointer;
    margin-left: 5px;
}